import React, { Component } from 'react'

import { Tab, Container, Row, Col, Nav, Form, FormControl, Button, Card, Navbar } from 'react-bootstrap'
import ReactPlayer from 'react-player'

import ArcCard from '../components/ArcCard';
import ArcTimeline from '../components/ArcTimeline';
import ArcGallery from '../components/ArcGallery';
import ArcMap from '../components/ArcMap';

import { useTranslation } from "react-i18next";

const players = [
    'https://youtu.be/217xXZC-ruc',
    'https://youtu.be/kasucTWw3SU',
]

export default function Tabs(props) {
    const { t, i18n } = useTranslation();
    return (
        <Tab.Container id='left-tabs-example' defaultActiveKey='first'>
            <Row className='tabs__container'>
                <Col className='mt-3' sm={3}>
                    <Card className='bg-dark'>
                        <Nav variant='pills' className='flex-column mt-2 mx-1'>
                            <Nav.Item>
                                <Nav.Link eventKey='first'>{t("1st-arcCard-name")}</Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link eventKey='second'>{t("2st-arcCard-name")}</Nav.Link>
                            </Nav.Item>
                        </Nav>
                    </Card>
                </Col>

                <Col sm={9}>
                    <Tab.Content className='mt-3'>
                        <Tab.Pane eventKey='first'>
                            <>
                                <ArcCard cardProp='0' />
                                <ArcTimeline timelineProp='0' />
                                <ArcGallery galleryProp='0' />

                                <ReactPlayer
                                    url={players[0]}
                                    width='100%'
                                    height='500px'
                                ></ReactPlayer>

                                <ArcMap mapProp='0' />
                            </>
                        </Tab.Pane>

                        {/*=============================*/}

                        <Tab.Pane eventKey='second'>
                            <>
                                <ArcCard cardProp='1' />
                                <ArcTimeline timelineProp='1' />
                                <ArcGallery galleryProp='1' />

                                <ReactPlayer
                                    url={players[1]}
                                    width='100%'
                                    height='500px'
                                ></ReactPlayer>

                                <ArcMap mapProp='1' />

                            </>
                        </Tab.Pane>
                    </Tab.Content>
                </Col>
            </Row>

        </Tab.Container>



    )
}
